import React, { useState } from 'react'

function Admin(props) {
  const [viewId, setviewId] = useState('')
  const handler=()=>
  {
    props.history.push(`/form/${viewId}`);
  }

  fetch("http://localhost:3001/getadmin")

  return (
    <div className='container d-flex justift-content-center align-items-center ' style={{height:'100vh'}}>
        <div className="input-group mb-3">
           <label className="input-group-text ">Enter View Id</label>      
           <input type="text" className="form-control " value={viewId} onChange={(e)=>setviewId(e.target.value)} />
           <br />
           <button type="button" className="btn btn-outline-secondary bg-dark bg-opacity-50 text-white m-2 " data-target="#mymodal" data-toggle="modal" onClick={handler}>Submit</button>
        </div>
    </div>
  )
}

export default Admin