import React, { useEffect, useState } from 'react'
import axios from 'axios';

function View(props) {
  console.log(props.match.params.s)
  const [data, setdata] = useState({data:{}})
    
        
        const GetData=()=>{
          axios.get(`http://localhost:3001/view/${props.match.params.s}/${props.match.params.e}/${props.match.params.id}`).then(res=>setdata(res));
          
          }
          //console.log(data.data['ga:users']);
          useEffect(() => {
           GetData();
          }, []);
        
         
   
  //console.log(data);
  return (
    <div><table className='table  table-dark text-white'>
           
               
    <tr className='text-white bg-dark'>
        <td>Users</td>
        <td>{data.data['ga:users']}</td> </tr>
     <tr className='text-white bg-dark'>
        <td>AvgSessionDuration</td>
        <td>{data.data['ga:avgSessionDuration']}</td> </tr>
        </table>
        </div>
  )
}

export default View