import { Route, Switch } from 'react-router-dom';
import Forum from './componnets/Forum';
import Home from './componnets/Home';
import Admin from './componnets/Admin';
import Bar from './componnets/Bar';
import View from './componnets/View';



function App() {
  return (
    <div className="App">
      <Bar/>
    <Switch>
      <Route exact path='/' component={Home}/>
      <Route  path='/form/:id' component={Forum}/>
      <Route  path='/admin' component={Admin}/>
      <Route  path='/view/:s/:e/:id' component={View}/>

      
      </Switch>
    </div>
  );
}

export default App;
