import React, { useState } from 'react'

function Forum(props) {
    const [start, setstart] = useState('');
    const [end, setend] = useState('')

    const handler=()=>{
        fetch('http://localhost:3001/getview')
        props.history.push(`/view/${start}/${end}/${props.match.params.id}`)
    }
  return (
    <div className='container text-center mt-5'><div class="form-group m-5">
    
    <label for="form_email">Start Date</label>{" "}
    <input
      id="form_date"
      type="date"
      name="date"
      class="form-control"
      value={start}
      placeholder="Please enter post date *"
      required="required"
      data-error="Valid date is required."
      onChange={(e) => setstart(e.target.value)}
    />
  </div>
  
  <div class="form-group m-5">
    
    <label for="form_email">End Date</label>{" "}
    <input
      id="form_date"
      type="date"
      name="date"
      class="form-control"
      value={end}
      placeholder="Please enter post date *"
      required="required"
      data-error="Valid date is required."
      onChange={(e) => setend(e.target.value)}
    />
    
  </div>
  <button type="button" className="btn btn-outline-secondary bg-dark bg-opacity-50 text-white m-2 " data-target="#mymodal" data-toggle="modal" onClick={handler}>Submit</button><hr></hr></div>
  )
}

export default Forum